.signin-form {
  width: 600px;
  background: #f5f5f5;
  border: 1px solid rgb(160, 160, 150);
  border-radius: 10px;
  border: 1px solid rgb(160, 160, 150);
  box-shadow: 0 15px 16.83px 0.17px rgba(0,0,0,.05);
  -moz-box-shadow: 0 15px 16.83px .17px rgba(0,0,0,.05);
}
.signin-form > div {
  padding: 50px 10px 10px;
}

h2 {
  margin-bottom: 20px;
  text-align: center;
  color: #212529;
}
.phone-prefix {
  display: block;
  float: left;
  padding: 15px 20px;
  font-size: 17px;
  border: 1px solid #ced4da;
  border-right: initial;
  width: 60px;
  box-sizing: border-box;
}
.phone {
  margin-bottom: 10px;
}
.phone label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.phone input {
  padding: 15px 20px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
  float: left;
  width: 240px;
}
.phone input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  //box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}

.signin-btn {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 300px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 40px;
}

.signin-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}

.alert {
  border-color: transparent;
}
.alert-success {
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 400px;
  margin: 20px auto;
}
