body {
  background: #f8f8f8;
}
.user-page {
  text-align: center;
  width: 600px;
  border: 1px solid rgb(160, 160, 150);
  border-radius: 10px;
  text-align: left;
  text-align: center;
  width: 400px;
  margin-top: 20px;
  background: #fff;
  margin: 0 auto;
  box-shadow: 0 15px 16.83px 0.17px rgba(0,0,0,.05);
  -moz-box-shadow: 0 15px 16.83px .17px rgba(0,0,0,.05);
  border: initial;
  border: 1px solid rgb(160, 160, 150);
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
  border: none;
}

.email {
  margin-bottom: 10px;
}
.email label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.email input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
}
.email input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  //box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.code {
  margin-bottom: 20px;
}
.code label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.code input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  //border-top-left-radius: 5px;
  //border-top-right-radius: 5px;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
}
.code input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}


.update-email-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 65px;
}
.update-email-btn {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 240px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  margin: auto;
  transform: scale(1);
}

.update-email-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.update-email-btn:active {
  transform: scale(0.9);
}

.request-confirmation-code-btn {
  color: #000;
  background-color: #fff;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 330px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  border: 1px solid #ced4da;
  border: 2px solid rgb(0, 119, 204);
  background-color: transparent;
  color: rgb(0, 119, 204);
  font-weight: 700;
}
.request-confirmation-code-btn:hover {
  background-color: rgba(0, 90, 158, 0.1);
  border-color: rgb(0, 90, 158);
  color: rgb(0, 90, 158);
  cursor: pointer;
}
.request-confirmation-code-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}


.alert {
  border-color: transparent;
  animation: fadein 2s;
  -moz-animation: fadein 2s; /* Firefox */
  -webkit-animation: fadein 2s; /* Safari and Chrome */
  -o-animation: fadein 2s; /* Opera */
}
.alert-success {
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
  width: auto;
}

.alert-error {
  color: red;
  //background-color: #d1e7dd;
  //border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
  width: auto;
}