body {
  background: #f8f8f8;
}
.user-page {
  text-align: center;
  width: 600px;
  border: 1px solid rgb(160, 160, 150);
  border-radius: 10px;
  text-align: left;
  text-align: center;
  width: 400px;
  margin-top: 20px;
  background: #fff;
  margin: 0 auto;
  box-shadow: 0 15px 16.83px 0.17px rgba(0,0,0,.05);
  -moz-box-shadow: 0 15px 16.83px .17px rgba(0,0,0,.05);
  border: initial;
  border: 1px solid rgb(160, 160, 150);
  margin-bottom: 40px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
  border: none;


  .user-page__name {
    font-weight: bold;
    font-size: 24px;
  }

  .user-page__email {
    color: rgba(119, 119, 119, 0.8);
  }

  p + p {
    margin-top: 5px;
  }

  .user-page__logout {
    width: 120px;
    cursor: pointer;
    padding: 10px 20px;
    margin-top: 20px;
    background: #fff;
    border: 1px solid #e5e5e5;
    margin-bottom: 10px;
  }

  .user-page__logout:focus {
    border-color: #86b7fe;
    outline: 0;
    box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
    position: relative;
  }
  .user-page__logout:active {
    transform: scale(0.9);
  }

  .user-page__avatar {
    border-radius: 50%;
    margin-bottom: 20px;
    width: 200px;
    height: 200px;
    cursor: pointer;
    border: 1px solid #a0a096;
    margin-top: 20px;
    margin-left: 60px;
    margin-left: 80px;
    margin-top: 40px;
    margin: 0;
    margin-top: 40px;
    margin-bottom: 10px;
    box-sizing: border-box;
    display: inline-block;
  }
  .user-page__avatar:hover {
    border: 1px solid;
  }
}

