.update-profile-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 65px;
}
.update-profile-btn {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 240px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  margin: auto;
  transform: scale(1);
}

.update-profile-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.update-profile-btn:active {
  transform: scale(0.9);
}


.alert {
  border-color: transparent;
  animation: fadein 2s;
  -moz-animation: fadein 2s; /* Firefox */
  -webkit-animation: fadein 2s; /* Safari and Chrome */
  -o-animation: fadein 2s; /* Opera */
}
.alert-success {
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
  width: auto;
}

.alert-error {
  color: red;
  //background-color: #d1e7dd;
  //border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto 30px;
  width: auto;
}

.chat-page {
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
}
.chat-page-content {
  display: flex;
  border: none;
  height: 500px;
  background: #fff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.chats-page-top-headbar {
  height: 20px;
}
.chats {
  width: 290px;
  border-right: 1px solid rgb(160, 160, 150);
}
.dialog {
  width: 400px;
  border-right: 1px solid rgb(160, 160, 150);
}
.receiver {
  width: 250px;
}
.chats-actions {
  margin-top: 10px;
  display: flex;
}
.chats-more-container {
  margin-left: 10px;
  margin-top: 15px;
  margin-right: 5px;
}
.chats-more-btn {
  padding: 5px 10px;
  color: #808589;
}
.chats-search-container {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 5px;
}
.chats-search {
  padding: 7px 10px;
  max-width: 100%;
  box-sizing: border-box;
  font-size: 17px;
  width: 215px;
  background: #f5f7fa;
  border: none;
}
.chats-latest {
  margin-top: 20px;
}
.chats-latest-item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  text-decoration: initial;
  color: initial;
  padding: 5px 0;
}
.chats-latest-item.active {
  background: rgb(41, 128, 185);
  color: #fff
}
.chats-latest-item img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  margin-right: 15px;
  margin-left: 10px;
}
.chats-latest-item span {
}
.dialog {
  display: flex;
  flex-direction: column;
  align-content: flex-end;
  justify-content: space-between;
}
.dialog-receiver {
  padding: 10px 10px;
  font-size: 14px;
}
.dialog-receiver-name {
  margin-bottom: 5px;
}
.dialog-receiver-last-seen {
  color: #bdc3c7;
}
.dialog-messages {
  padding: 10px 14px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  flex-grow: 2;
  overflow: auto;
}
.dialog-messages-message {
  padding: 10px;
  margin-bottom: 7px;
  border-radius: 5px;
}
.dialog-messages-message.from-user {
  border: 1px solid transparent;;
  background: #effdde;
  align-self: flex-end;
}
.dialog-messages-message.from-receiver {
  align-self: flex-start;
  border: 1px solid lightgray;
  background: #fff;
}
.dialog-messages-message-datetime {
  display: block;
  font-size: 14px;
  margin-top: 5px;
}

.dialog-messages-new-message-container {
  display: flex;
  background: #f5f7fa;
  padding-top: 10px;
  padding-bottom: 5px;
}
.dialog-messages-add-attachment-btn {
  margin-bottom: 5px;
  margin-left: 10px;
  margin-right: 5px;
  margin-top: 5px;
  font-size: 24px;
  color: #808589;
}
.dialog-messages-new-message {
  justify-self: flex-start;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
  border: initial;
  background: #f5f7fa;
}
.dialog-messages-submit-btn {
  margin-bottom: 5px;
  margin-right: 10px;
  margin-left: 5px;
  font-size: 24px;
  margin-top: 5px;
  color: #808589;
}
.receiver-content {
  padding: 15px 20px;
  box-sizing: border-box;
}
.receiver-content > span {
  display: block;
  margin-bottom: 20px;
  font-size: 19px;
}
.receiver-person {
  display: flex;
}
.receiver-person-avatar {
  width: 75px;
  height: 75px;
  border-radius: 50px;
  margin-right: 20px;
}
.receiver-person-name {
  align-self: center;
  margin-bottom: 5px;
}
.receiver-person-last-seen {
  color: #bdc3c7;
  font-size: 14px;
}
.receiver-divider {
  background: #f5f7fa;
  height: 20px;
  border-top: 1px solid #dfdfdf;;
}
.receiver-phone {
  padding-left: 40px;
  padding-top: 20px;
}
.receiver-phone-value {
  font-size: 14px;
  margin-bottom: 5px;
}
.receiver-phone-label {
  font-size: 14px;
