.vk-button__input {
  display: flex;
  align-items: center;
  color: #4680c2;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  padding-right: 20px;
  cursor: pointer;
  outline: 0;
  margin: auto;

  &:hover {
    opacity: 0.8;
  }
}
.vk-button__input:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}

.vk-button {
  text-align: center;

  width: 300px;
  margin: auto;
  margin-bottom: 20px;
}

.vk-button__input-icon {
  margin-right: 10px;
  width: 60px;
}
