body {
  background: #f8f8f8;
}
.user-page {
  text-align: center;
  width: 600px;
  border: 1px solid rgb(160, 160, 150);
  border-radius: 10px;
  text-align: left;
  text-align: center;
  width: 400px;
  margin-top: 20px;
  background: #fff;
  margin: 0 auto;
  box-shadow: 0 15px 16.83px 0.17px rgba(0,0,0,.05);
  -moz-box-shadow: 0 15px 16.83px .17px rgba(0,0,0,.05);
  border: initial;
  border: 1px solid rgb(160, 160, 150);
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
  border: none;
}

.first-name {
  margin-bottom: 10px;
  display: flex;
  justify-content: flex-start;
  margin-left: 60px;
}
.first-name label {
}
.first-name span {
  margin-bottom: 5px;
  display: block;
  font-size: 17px;
  text-align: left;
}
.first-name input {
  padding: 10px;
  font-size: 17px;
  width: 270px;
  border-color: #e5e5e5;
  border-width: 1px;
  box-sizing: border-box;
  border-radius: 5px;
}

.first-name input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
  position: relative;
}


