.update-profile-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 65px;
}
.update-profile-btn {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 240px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  margin: auto;
  transform: scale(1);
  font-weight: 700;
}

.update-profile-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.update-profile-btn:active {
  transform: scale(0.9);
}