.registration-page {

}
.registration-form {
  width: 600px;
  background: #fff;
  border: 1px solid rgb(160, 160, 150);
  box-shadow: 0 15px 16.83px 0.17px rgba(0,0,0,.05);
  -moz-box-shadow: 0 15px 16.83px .17px rgba(0,0,0,.05);
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
  border: none;
}
.registration-form > div {
  padding: 50px 10px 10px;
}

h2 {
  margin-bottom: 20px;
  text-align: center;
  color: #212529;
}

.first-name {
  margin-bottom: -1px;
}
.first-name label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.first-name input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
}
.first-name input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
