.phone-prefix {
  display: block;
  float: left;
  padding: 15px 20px;
  font-size: 17px;
  border: 1px solid #ced4da;
  border-right: initial;
  width: 60px;
  box-sizing: border-box;
}
.phone {
  margin-bottom: -1px;
}
.phone label {
  width: 300px;
