  width: 480px;
  margin: 20px auto;
}

.alert-error {
  color: red;
  //background-color: #d1e7dd;
  //border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
}

@keyframes shakeMe {
  0%, 100% {
    transform: translateX(0);
  }
  20%,
  60% {
    transform: translateX(-10px);
  }
  40%,
  80% {
    transform: translateX(10px);
  }
}


/* Applying the animation */
.has-error {
  animation-name: shakeMe;
  animation-duration: .5s;
}

