.user-page {
  text-align: center;
  width: 600px;
  border: 1px solid rgb(160, 160, 150);
  border-radius: 10px;
  text-align: left;
  text-align: center;
  width: 400px;
  margin-top: 20px;
  background: #fff;
  margin: 0 auto;
  box-shadow: 0 15px 16.83px 0.17px rgba(0,0,0,.05);
  -moz-box-shadow: 0 15px 16.83px .17px rgba(0,0,0,.05);
  border: initial;
  border: 1px solid rgb(160, 160, 150);
  padding-top: 20px;
  padding-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
  border: none;
}


.phone-prefix {
  display: block;
  float: left;
  padding: 15px 20px;
  font-size: 17px;
  border: 1px solid #ced4da;
  border-right: initial;
  width: 60px;
  box-sizing: border-box;
}
.phone {
  margin-bottom: 10px;
}
.phone label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.phone input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
  width: 240px;
}
.phone input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  //box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}


.password {
  display: flex;
  justify-content: flex-start;
  margin-left: 60px;
}
.password label {
}
.password span {
  margin-bottom: 5px;
  display: block;
  font-size: 17px;
  text-align: left;
}
.password input {
  padding: 10px;
  font-size: 17px;
  width: 270px;
  border-color: #e5e5e5;
  border-width: 1px;
  box-sizing: border-box;
  border-radius: 5px;
  display: block;
  margin-bottom: 5px;
}

.password input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
  position: relative;
}
.password a {
  font-size: 17px;
  //margin-bottom: 10px;
  //margin-left: 50px;
  display: block;
  color: #006edc;
  height: 30px;
  line-height: 30px;
  text-align: left;
  padding: 0 10px;
  display: inline-block;
}
.password a:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
  position: relative;
}

.code {
  margin-bottom: 20px;
}
.code label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.code input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  //border-top-left-radius: 5px;
  //border-top-right-radius: 5px;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
}
.code input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}


.update-email-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 65px;
}
.update-email-btn {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 240px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  margin: auto;
  transform: scale(1);
  font-weight: 700;
}

.update-email-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.update-email-btn:active {
  transform: scale(0.9);
}

.request-confirmation-code-btn {
  color: #000;
  background-color: #fff;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 300px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  border: 1px solid #ced4da;
}
.request-confirmation-code-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}


.alert {
  border-color: transparent;
  animation: fadein 2s;
  -moz-animation: fadein 2s; /* Firefox */
  -webkit-animation: fadein 2s; /* Safari and Chrome */
  -o-animation: fadein 2s; /* Opera */
}
.alert-success {
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
  width: auto;
}

.alert-error {
  color: red;
  //background-color: #d1e7dd;
  //border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
  width: auto;
}


