.signin-form {
  width: 600px;
  background: #fff;
  border: 1px solid rgb(160, 160, 150);
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1), 3px 10px 30px rgba(0,0,0,0.08);
  border: none;
}
.signin-form > div {
  padding: 50px 10px 10px;
}

h2 {
  margin-bottom: 20px;
  text-align: center;
  color: #212529;
}

.phone-prefix {
  display: block;
  float: left;
  padding: 15px 20px;
  font-size: 17px;
  border: 1px solid #ced4da;
  border-right: initial;
  width: 60px;
  box-sizing: border-box;
}
.phone {
  margin-bottom: -1px;
}
.phone label {
  width: 300px;
  margin: auto;
  display: block;
  font-size: 17px;
}
.phone input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ced4da;
  border-width: 1px;
  z-index: 2;
  //border-top-left-radius: 5px;
  //border-top-right-radius: 5px;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
  outline: none;
  width: 240px;
}
.phone input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  //box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.password {
  margin-bottom: 20px;
}
.password label {
  width: 300px;
  margin: auto;
  display: block;
}
.password input {
  padding: 15px 10px;
  font-size: 17px;
  width: 300px;
  box-sizing: border-box;
  border-top: 1px solid transparent;
  border: 1px solid #ced4da;
  border-width: 1px;
  //border-bottom-left-radius: 5px;
  //border-bottom-right-radius: 5px;
  height: auto;
  background-color: #fff;
  background-clip: padding-box;
}
.password input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}

.signin-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100px;
}
.signin-btn {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  padding: 1rem 0.5rem;
  font-size: 1.25rem;
  border-radius: .3rem;
  width: 300px;
  margin: auto;
  text-align: center;
  box-sizing: border-box;
  display: block;
  border: 0;
  margin-bottom: 10px;
  margin: auto;
  transform: scale(1);
}

.signin-btn:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgba(13,110,253,.25);
  position: relative;
}
.signin-btn:active {
  transform: scale(0.9);
}

.alert {
  border-color: transparent;
  animation: fadein 2s;
  -moz-animation: fadein 2s; /* Firefox */
  -webkit-animation: fadein 2s; /* Safari and Chrome */
  -o-animation: fadein 2s; /* Opera */
}
.alert-success {
  color: #0f5132;
  background-color: #d1e7dd;
  border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
}

.alert-error {
  color: red;
  //background-color: #d1e7dd;
  //border-color: #badbcc;
  padding: 15px;
  font-size: 17px;
  width: 480px;
  margin: 20px auto;
  text-align: center;
}


@keyframes shakeMe {
  0%, 100% {
    transform: translateX(0);
  }
  20%,
  60% {
    transform: translateX(-10px);
  }
  40%,
  80% {
    transform: translateX(10px);
  }
}


/* Applying the animation */
.has-error {
  animation-name: shakeMe;
  animation-duration: .5s;
}